export function sortBy<T>(items: T[], selector: (item: T) => number | string, invert = false) {
    const result = [...items];
    const desc = (a, b) => a > b ? -1 : a < b ? 1 : 0;
    const asc = (a, b) => a < b ? -1 : a > b ? 1 : 0;
    
    const func = !invert ? desc : asc;

    result.sort((a, b) => func(selector(a), selector(b)));
    return result;
}
