import BaseModel, {Map, Model, propertyCollection} from ".";

export class Weapon<T extends BaseModel> extends BaseModel {
    readonly name: string;
    readonly id: string;

    readonly variants: T[] = [];
    
    constructor(name?: string) {
        super();
        this.name = name;
        this.id = name ? name.toLowerCase().replace(/\s/g, '-') : null;
    }

    getSortKey(): string {
        return this.name;
    }
}

export abstract class BaseWeaponVariant extends BaseModel {
    attackType: AttackType;

    get attackTypeLabel() {
        switch (this.attackType) {
             case AttackType.Strike:
                 return 'Strike';
             case AttackType.Thrust:
                 return 'Thrust';          
             case AttackType.Projectile:
                return 'Projectile';                
        }
    }  

    protected resolveHandedness(value: string) {
        switch (value) {
            case 'One Handed':
                return Handedness.One;
    
            case 'Two Handed':
                return Handedness.Two;
    
            case 'One/Two Handed':
                return Handedness.One | Handedness.Two;
        }
    }

    protected resolveAttackType(value: string) {
        switch (value) {
            case 'Strike':
                return AttackType.Strike;
            case 'Thrust':
                return AttackType.Thrust;            
        }
    }
}


@Model
export class MeleeWeapon extends BaseWeaponVariant {
    id: string;
    attackType: AttackType;
    handedness: Handedness;
    
    @Map([null, 'Name'])
    name: string;

    @Map([null, 'Attack'])
    attack: string;

    @Map([null, 'Alt Mode'], o => o.toLowerCase() == 'on')
    altMode: boolean;

    @Map([null, 'Type'])
    type: string;

    @Map([null, 'Stop On Hit'], o => o.toLowerCase() == 'yes')
    stopOnHit: boolean;

    @Map([null, 'Can Combo'], o => o.toLowerCase() == 'yes')
    canCombo: boolean;

    @Map([null, 'Can Flinch'], o => o.toLowerCase() == 'yes')
    canFlinch: boolean;

    @Map([null, 'Point Cost'], parseInt)
    pointCost: number;

    @Map([null, 'Value'], parseFloat)
    value: number;

    @Map(['Damage [No Armor]', {head: 'Head', torso: 'Torso', legs: 'Legs'}], propertyCollection(parseFloat))
    damageNoArmor: WeaponDamage;

    @Map(['Damage [Light Armor]', {head: 'Head', torso: 'Torso', legs: 'Legs'}], propertyCollection(parseFloat))
    damageLightArmor: WeaponDamage;

    @Map(['Damage [Medium Armor]', {head: 'Head', torso: 'Torso', legs: 'Legs'}], propertyCollection(parseFloat))
    damageMediumArmor: WeaponDamage;

    @Map(['Damage [Heavy Armor]', {head: 'Head', torso: 'Torso', legs: 'Legs'}], propertyCollection(parseFloat))
    damageHeavyArmor: WeaponDamage;
    
    @Map([null, {averageDamage: 'Avg. Damage', dps: 'DPS', cdps: 'CDPS', averageDps: 'Avg. DPS'}], propertyCollection(parseFloat))
    computedDamage: {
        averageDamage: number;
        dps: number;
        cdps: number;
        averageDps: number;
    };
    
    @Map(['Stamina', {miss: 'Miss Cost', feint: 'Feint Cost', morph: 'Morph Cost', drain: 'Stamina Drain', parryDrainNegation: 'Parry Drain Negation'}], propertyCollection(parseFloat))
    stamina: {
        miss: number; 
        feint: number;
        morph: number;
        drain: number;
        parryDrainNegation: number;
    };

    @Map(['Speed', {windup: 'Windup', combo: 'Combo', release: 'Release', recovery: 'Recovery'}], propertyCollection(parseFloat))
    speed: {
        windup: number;
        combo: number;
        release: number;
        recovery: number;
    };

    @Map(['Speed', {attack: 'Attack Speed', combo: 'Combo Speed'}], propertyCollection(parseFloat))
    computedSpeed: {
        attack: number;
        combo: number;
    };

    @Map([null, 'Length'], parseFloat)
    length: number;

    @Map([null, 'Peasant Perk'], o => o != null && o.toLowerCase() == 'yes')
    isPeasantWeapon: boolean;

    getSortKey() {
        return this.name + this.attack + (this.altMode ? 'B' : 'A');
    }
    
    constructor(data?: string[]) {
        super(data);
        if (data == null) {
            return;
        }

        this.attackType = this.resolveAttackType(this.attack);
        this.handedness = this.resolveHandedness(this.type);

        this.id = `${this.name.toLowerCase().replace(/ /g, '-')}-${this.attackType}${this.altMode ? '-alt' : ''}`;
    }

}

export enum DamageTarget {
    Head = 1 << 0,
    Torso = 1 << 1,
    Legs = 1 << 2,
}

export enum ArmorLevel {
    None = 0,
    Light = 1 << 0,
    Medium = 1 << 1,
    Heavy = 1 << 2,
}

export class WeaponDamage {
    head: number;
    torso: number;
    legs: number;
}

export enum Handedness {
    One = 1 << 0,
    Two = 1 << 1,
}

export enum AttackType {
    Strike =        1 << 0,
    Thrust =        1 << 1,
    Projectile =    1 << 2,
}

