
































    import Vue from 'vue';
    import { Component, Watch, Prop } from 'vue-property-decorator';
    import { DisplayWeapon } from '../../ts/view-model/weapon';
    import AttributeBar from './attribute-bar.vue';
    import WeaponInventoryCost from './weapon-inventory-cost.vue';
    import WeaponLabel from './weapon-label.vue';
    import { sortBy } from '../../ts/utils/sort';
import { DamageTarget, ArmorLevel, MeleeWeapon } from '../../ts/model/weapon';
import Projectile from '../../ts/model/projectile';

    @Component({components: {AttributeBar, WeaponLabel, WeaponInventoryCost}})
    export default class AttributeBarContainer extends Vue {

        @Prop()
        title: string;

        @Prop()
        iconClass: string;

        @Prop()
        colorClass: string;

        @Prop()
        invert: boolean;

        @Prop()
        sort: boolean;

        @Prop()
        globalCompare: boolean;

        @Prop()
        allItems: DisplayWeapon[];

        @Prop()
        items: DisplayWeapon[];

        @Prop()
        displayValue: (item: any) => number;

        @Prop()
        prefixStyle: any;

        // defaults to a function that accesses 'id' property of the item
        @Prop({type: Function, default: (item) => item.id })
        itemKey: Function;

        @Prop({type: Function, default: () => true})
        isEnabled: Function;

        @Prop()
        damageTarget: DamageTarget;

        @Prop()
        armorLevel: ArmorLevel;

        @Prop()
        fixedMax: number;

        get sortValues() {
            return this.sortLocally || (this.sort && this.sortLocally);
        }

        sortLocally: boolean = false;
        max = 0;
        displayItems: MeleeWeapon | Projectile[] = [];

        mounted() {
            this.updateMaxAndDisplayItems();
        }
        
        @Watch('items')
        @Watch('sort')
        @Watch('sortLocally')
        @Watch('globalCompare')
        @Watch('damageTarget')
        @Watch('armorLevel')
        updateMaxAndDisplayItems() {
            const displayItems: (MeleeWeapon | Projectile)[] = [];

            for (let item of this.items) {
                for (let variant of item.variants) {
                    displayItems.push(variant);
                }
            }
            
            if (this.fixedMax == null) {
                const pool = this.globalCompare ? this.allItems : this.items;
                const values: number[] = [];

                for (let item of pool) {
                    for (let variant of item.variants) {
                        const value = this.displayValue(variant);
                        if (value != null && !isNaN(value)) {
                            values.push(value);
                        }
                    }
                }
                this.max = (this.invert ? Math.min : Math.max)(...values);
            } else {
                this.max = this.fixedMax;
            }
            
            this.displayItems = this.sortValues ? sortBy(displayItems, this.displayValue, this.invert) : displayItems;
        }

        @Watch('sort')
        onSortValuesChanged() {
            this.sortLocally = this.sort;
        }

        formula(item: any) {
            const value = this.displayValue(item);

            return this.invert ? this.max / value : value  / this.max;
        }

    }
