import { Weapon, Handedness, MeleeWeapon } from "../model/weapon";
import Projectile from "../model/projectile";
import {buildUrl, getCacheKey, SheetsApiMethod} from '../../js/common/data';

export default class DataProvider2 {
    cachingDurationSeconds: number;
    public spreadsheet: Spreadsheet;
    private readonly googleApiKey: string;
    private readonly version: string;
    private readonly fallbackDataPath: string;
    public isFallbackDataBeingUsed: boolean = false;

    constructor(googleApiKey: string, version: string, fallbackDataPath: string, cachingDurationSeconds = 60 * 60) {
        this.version = version;
        this.googleApiKey = googleApiKey;
        this.fallbackDataPath = fallbackDataPath;
        this.cachingDurationSeconds = cachingDurationSeconds;
    }

    public async init() {
        this.spreadsheet = await this.getSpreadsheet();
    }

    private async requestCached<T extends SheetsApiResponse>(method: string, params = '') {
        const key = getCacheKey(this.version, method, params);
        const cached: LocalStorageCachedResponse = JSON.parse(localStorage.getItem(key) || 'null');

        if (cached && new Date(cached.expiresAt).getTime() >= new Date().getTime() && cached.version == this.version) {
            return <T>cached.data;
        }
        
        const url = buildUrl(this.googleApiKey, method, params);
        let data: T;
        let cacheFetchedData = true;

        try {
            const request = await fetch(url);
            const expectedStatusCode = 200;
            if (request.status != expectedStatusCode) {
                throw new Error(`Unexpected response status code of ${request.status} - expected ${expectedStatusCode}.`);
            }
            
            data = <T>JSON.parse(await request.text());
        } catch (error) {
            console.warn('An error occurred while loading spreadsheet data - using fallback.');
            this.isFallbackDataBeingUsed = true;

            const request = await fetch(`/${this.fallbackDataPath}/${key}`);
            data = <T>JSON.parse(await request.text());
            // Fallback data should never be cached
            cacheFetchedData = false;
        }

        if (data.error == null && cacheFetchedData) {
            const expiresAt = new Date;
            expiresAt.setSeconds(expiresAt.getSeconds() + this.cachingDurationSeconds);

            localStorage.setItem(key, JSON.stringify(<LocalStorageCachedResponse>{
                data: data,
                expiresAt: expiresAt.toISOString(),
                version: this.version
            }));
        }

        return data;
    }

    async getSpreadsheet<T extends Spreadsheet>() {
        return await this.requestCached(SheetsApiMethod.Get) as T;
    }

    async getSheetData<T extends SheetValues>(name: string) {
        return await this.requestCached(SheetsApiMethod.Values, name) as T;
    }

    async getWeapons(): Promise<Weapon<MeleeWeapon | Projectile>[]> {
        const [meleeWeaponsData, projectileWeaponData] = await Promise.all([
            this.getSheetData<{values: (string[])[]}>(this.spreadsheet.sheets.find(o => o.properties.title.toLowerCase().startsWith('melee')).properties.title),
            this.getSheetData<{values: (string[])[]}>(this.spreadsheet.sheets.find(o => o.properties.title.toLowerCase().startsWith('ranged')).properties.title)
        ]);

        const result: Weapon<MeleeWeapon | Projectile>[] = [];
        const weapons: {[key: string]: Weapon<MeleeWeapon | Projectile>} = {};
        
        const meleeHeaderRows = [meleeWeaponsData.values[0], meleeWeaponsData.values[1]];
        const projectileHeaderRows = [projectileWeaponData.values[0], projectileWeaponData.values[1]];

        MeleeWeapon.Schema.generate(meleeHeaderRows);
        Projectile.Schema.generate(projectileHeaderRows);

        const parseWeapons = (rows: (string[])[], variantClass: typeof MeleeWeapon | typeof Projectile, headerRows: any[], data: {values: (string[])[]}) => {
            let rowIndex = -1;
    
            for (let row of rows) {
                rowIndex++;
                if (rowIndex < headerRows.length) {
                    continue;
                }

                const variant = new variantClass(row);
                if (!(variant.name in weapons)) {
                    weapons[variant.name] = new Weapon(variant.name);
                }
    
                weapons[variant.name].variants.push(variant);
            }
        };

        parseWeapons(meleeWeaponsData.values, MeleeWeapon, meleeHeaderRows, meleeWeaponsData);
        parseWeapons(projectileWeaponData.values, Projectile, projectileHeaderRows, projectileWeaponData);
        
        for (let name in weapons) {
            weapons[name].variants.sort(MeleeWeapon.sorter);
            result.push(weapons[name]);
        }

        result.sort(Weapon.sorter);
        return result;
    }
}

export interface SheetsApiResponse {
    error?: {
        code: string;
        message: string;
    }
}

export interface SheetValues extends SheetsApiResponse {
    values?: any[];
}

export interface Spreadsheet extends SheetsApiResponse {
    spreadsheetId: string;
    properties: {
        title: string;
        locale: string;
        autoRecalc: string;
        timeZone: string;
        defaultFormat: any;
    }

    sheets: {
        properties: {
            sheetId: string;
            title: string;
            index: number;
            sheetType: string;
        }
    }[];

    spreadsheetUrl: string;
}

interface LocalStorageCachedResponse {
    expiresAt: string;
    version: string; 
    data: any;
}