export default abstract class BaseModel {
    static Schema: Schema;

    constructor(data?: string[]) {
        if (data) {
            (this.constructor as typeof BaseModel).Schema.populate(this, data);
        }
    }
    
    abstract getSortKey(): string;

    public static sorter<T extends BaseModel>(a: T, b: T) {
        const _a = a.getSortKey();
        const _b = b.getSortKey();

        if (_a < _b) return -1;
        if (_a > _b) return 1;
        return 0;
    }
}

export class Schema {
    private fields: SchemaField[] = [];

    constructor(fields: SchemaField[]) {
        this.fields = fields;
    }

    private resolveColumnIndex(headerRows: (string[])[], field: SchemaField): SchemaField['columnIndex'] {
        let isObject = false;

        const result = {};

        let startIndex = 0;

        for (let i = 0; i < headerRows.length; i++) {
            const headerRow = headerRows[i];
            const map = field.mapping[i];

            if (map != null) {
                if (typeof map == 'string') {
                    startIndex = headerRow.indexOf(map, startIndex);
                } else {
                    isObject = true;

                    for (let key in map) {
                        result[`${field.name}.${key}`] = headerRow.indexOf(map[key], startIndex);
                    }
                }
            }
        }
        
        if (!isObject) {
            result[field.name] = startIndex;
        }

        return result;
    }

    generate(headerRows: (string[])[]) {
        for (let field of this.fields) {
            field.columnIndex = this.resolveColumnIndex(headerRows, field);
        }
    }
   
    add(field: SchemaField) {
        this.fields.push(field);
    }

    populate(instance: BaseModel, data: string[]) {
        for (let field of this.fields) {
            for (let path in field.columnIndex) {
                let value = data[field.columnIndex[path]];

                const pathParts = path.split('.');
                let targetObject: any = instance;

                for (let i = 0; i < pathParts.length; i++) {
                    const part = pathParts[i];

                    if (i < pathParts.length - 1 && pathParts.length > 1) {
                        if (targetObject[part] == null) {
                            targetObject[part] = {};
                        }

                        targetObject = targetObject[part];

                    } else {
                        targetObject[part] = value;
                    } 
                    
                }

                if (field.converter) {
                    const fullValue = instance[pathParts[0]];
                    instance[pathParts[0]] = (field.converter as any)(fullValue);
                }
            }
        }
    }
}

type SchemaColumn = string | null | {[key: string]: string};

export type SchemaMapping = (SchemaColumn | SchemaColumn)[];

export interface SchemaField {
    model: typeof BaseModel;
    name: string;
    mapping: SchemaMapping;
    columnIndex?: {[key: string]: number};
    converter: Converter<any>;
}

const __schemaMappingTempStore: {prototype: any, field: SchemaField}[] = [];

export type RawObject<T> = {
    [P in keyof T]: string;
}

export type Converter<T> = (data: RawObject<T> | string) => any;

export function Map<T = string>(mapping: SchemaMapping, converter: Converter<T> = null) {
    return function(target, propertyName: string) {
        const model = target as typeof BaseModel;
        
        __schemaMappingTempStore.push({
            prototype: target,
            field: {
                mapping: mapping,
                model: model,
                name: propertyName,
                converter: converter,
            }
        })
    }
}

export function Model<T extends {new(...args:any[]):{}}>(constructor: T) {
    const schemaFields: SchemaField[] = [];

    for (let i = __schemaMappingTempStore.length - 1; i >= 0; i--) {
        const field = __schemaMappingTempStore[i];

        if (field.prototype == constructor.prototype) {
            schemaFields.push(field.field);
            __schemaMappingTempStore.splice(i, 1);
        } 
    }

    return class extends constructor {
        static Schema = new Schema(schemaFields);
    }
}


export function propertyCollection<T>(func: (value: string) => any) {
    return (data: RawObject<T>) => {
        for (let key in data) {
            data[key] = func(data[key]);
        }

        return data;
    }
}