//@ts-check

var helpers = require('./helpers');
var slugify = helpers.slugify;

var SheetsApiMethod = {
    Get: 'Get',
    Values: 'Values'
};

var SpreadsheetId = '1HXS_aF9CKeGKg6OecwCw3WobZWcTjDkp_4ipN1vnyVk';


/**
 * Builds an URl for retrieving spreadsheet data.
 * @param {string} method 
 * @param {string} params 
 * @returns {string}
 */
function buildUrl(googleApiKey, method, params) {
    params = params || '';
    var apiMethodPath = '';
    switch (method) {
        // Request to get spreadsheet meta data
        case SheetsApiMethod.Get:
            apiMethodPath = '';
            break;
        // Request to get spreadsheet values
        case SheetsApiMethod.Values:
            apiMethodPath = '/values/';
            break;
        default:
            throw new Error('Unsupported API method ' + method);                        
    }

    return [
        'https://sheets.googleapis.com/v4/spreadsheets/', 
        SpreadsheetId,
        apiMethodPath,
        params,
        '?key=',
        googleApiKey
    ].join('');
}

function getCacheKey(version, method, params) {
    params = params || '';
    return slugify([
        SpreadsheetId, '-',
        version, '-',
        method,
        params != null && params.length > 0 ? '-' : '',
        params,
    ].join('')) + '.json';
}

module.exports = {
    buildUrl: buildUrl, 
    getCacheKey: getCacheKey, 
    SheetsApiMethod: SheetsApiMethod, 
    SpreadsheetId: SpreadsheetId
};
